<template>
  <div>
    <div class="search-header">
        <el-select class="select" v-model="value" placeholder="请选择" @change="sendValue">
            <el-option
                v-if="pageType==0"
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
            </el-option>
            <el-option
                v-if="pageType==1"
                v-for="item in options1"
                :key="item.value"
                :label="item.label"
                :value="item.value">
            </el-option>
            <el-option
                v-if="pageType==2"
                v-for="item in options2"
                :key="item.value"
                :label="item.label"
                :value="item.value">
            </el-option>
            <el-option
                v-if="pageType==3"
                v-for="item in options3"
                :key="item.value"
                :label="item.label"
                :value="item.value">
            </el-option>
            <el-option
                v-if="pageType==4"
                v-for="item in options4"
                :key="item.value"
                :label="item.label"
                :value="item.value">
            </el-option>
            <el-option
                v-if="pageType==5"
                v-for="item in options5"
                :key="item.value"
                :label="item.label"
                :value="item.value">
            </el-option>
        </el-select>
        <div class="input">
            <input type="text" @input="sendValue" v-model="inputText" class="el-input__inner"/>
        </div>
        <!-- <el-input class="input" v-model="inputText" @change="sendValue"></el-input> -->
        <el-button class="button" @click="search">搜索</el-button>
        <el-button class="button" @click="reset">重置</el-button>
        <el-button class="button" @click="addContract" v-if="pageType==1">添加合同</el-button>
    </div>
  </div>
</template>

<script>
export default {
  props: ['pageType'],
  data() {
    return {
        options: [
            {
                value: '选项1',
                label: '客户号'
            }, {
                value: '选项2',
                label: '客户名称'
            }, {
                value: '选项3',
                label: '仓单编号'
            }, {
                value: '选项4',
                label: '仓单品种'
            }, {
                value: '选项5',
                label: '合同编号'
            }
        ],
        options1: [
            {
                value: '选项1',
                label: '所属客户'
            },{
                value: '选项3',
                label: '仓单编号'
            }, {
                value: '选项5',
                label: '合同编号'
            }
        ],
        options2: [
            {
                value: '选项1',
                label: '客户号'
            }, {
                value: '选项2',
                label: '客户名称'
            }, {
                value: '选项3',
                label: '仓单编号'
            }
        ],
        options3: [
            {
                value: 'id',
                label: '客户号'
            }, {
                value: 'cname',
                label: '客户名称'
            }
        ],
        options4: [
            {
                value: '选项1',
                label: '合同编号'
            }, {
                value: '选项2',
                label: '仓单编号'
            }, {
                value: '选项3',
                label: '交易客户'
            }, {
                value: '选项4',
                label: '仓单货物品种'
            }
        ],
        options5: [
            {
                value: '选项1',
                label: '客户号'
            }, {
                value: '选项2',
                label: '客户名称'
            }, {
                value: '选项3',
                label: '合同编号'
            }
        ],
        value: '',
        inputText: '',

    };
  },
  mounted() {
      
  },
  methods: {
      sendValue(){
          this.$emit('getOption', this.value, this.inputText);
      },
      reset() {
          this.value='';
          this.inputText='';
          this.$emit('clearSearchInfo');
      },
      search(){
          this.$emit('searchInfo');
      },
      addContract(){
          this.$emit('openDialog', '', 'contract');
      }
  },
  components: {}
};
</script>

<style>
.search-header {
  position: relative;
  box-sizing: border-box;
  width: 100%;
  height: 70px;
  font-size: 22px;
  color: #fff;
  background-color: rgb(50, 65, 87);
}
.select {
    margin-top: 18px;
    margin-left: 8px;
    width: 120px;
    float: left;
}
.input {
    width: 200px;
    float: left;
    margin-top: 18px;
    margin-right: 15px;
    font-size: 16px
}
.input input {
    height:32px;
    line-height: 32px;
}
.button{
    margin-top: 18px;
}
</style>



